a {
  text-decoration: none;
  color: #E0ECFF;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 72px;
}
.header .menu {
  display: flex;
}
.header .left {
  display: flex;
  align-items: center;
}
.header .left .logo {
  margin-left: 48px;
  display: flex;
  align-items: center;
}
.header .left .logo > a {
  color: #E0ECFF;
  text-decoration: none;
  display: flex;
}
.header .left .logo .name {
  margin: auto 12px;
  color: #E0ECFF;
  font-size: 20px;
}
.header .right {
  display: flex;
  margin-right: 24px;
}
.header .small-menu {
  margin-left: 24px;
}
@media only screen and (min-width: 320px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .menu {
    display: none;
  }
  .header .small-menu {
    display: block;
  }
  .header .lang-select,
  .header .network-select {
    display: none;
  }
}
@media only screen and (min-width: 375px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .menu {
    display: none;
  }
  .header .small-menu {
    display: block;
  }
  .header .lang-select,
  .header .network-select {
    display: none;
  }
}
@media only screen and (min-width: 760px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .menu {
    display: none;
  }
  .header .small-menu {
    display: block;
  }
  .header .lang-select,
  .header .network-select {
    display: flex;
  }
}
@media only screen and (min-width: 1140px) {
  .header .menu {
    display: flex!important;
  }
  .header .small-menu {
    display: none;
  }
  .header .lang-select,
  .header .network-select {
    display: flex;
  }
}
@media only screen and (min-width: 1568px) {
  .header .menu {
    display: flex!important;
  }
  .header .small-menu {
    display: none;
  }
  .header .lang-select,
  .header .network-select {
    display: flex;
  }
}
@media only screen and (min-width: 2561px) {
  .header .menu {
    display: flex!important;
  }
  .header .small-menu {
    display: none;
  }
  .header .lang-select,
  .header .network-select {
    display: flex;
  }
}
